import request from "../../../request.js"

//id
function getPreOrderById(orderId){
  return request({
    url:`/lejuClient/order/getPreOrderById/${orderId}`,
  })
}
//id
function orderReturn(returnId){
  return request({
    url:`lejuClient/orderReturn/${returnId}`,
  })
}


export {
	getPreOrderById,
	orderReturn
  }