import request from "../../request.js"

//
function getPreOrderById(orderId){
  return request({
    url:`/lejuClient/order/getPreOrderById/${orderId}`,
  })
}
// 
function payConfirm(data){
  return request({
    url:"/lejuClient/order/payConfirm",
	method:"post",
	data
  })
}




export {
	getPreOrderById,
	payConfirm
  }