import request from "../request.js"

//
function getPreOrderById(orderId){
  return request({
    url:`/lejuClient/order/getPreOrderById/${orderId}`,
  })
}
//
function addConfirmOrder(data){
  return request({
    url:"/lejuClient/order/addConfirmOrder",
	method:'post',
	data
  })
}


export {
	getPreOrderById,
	addConfirmOrder
  }