var obj ={
	data(){
		return {
			
		}
	},
	methods:{
		// 
		checkLogin(){
			let token =uni.getStorageSync("token");
			if(!token){
				uni.showModal({
					 title:"",
					 content:",",
					 success(res) {
					 	console.log(res)
						if(res.confirm){//,
							uni.navigateTo({
								url:"/pages/mine/login/login"
							})
						}else{
							uni.showToast({
								title:"",
								icon:"success"
							})
						}
					 }
				})
				return false
			}else{
				return true
			}
		}
	}
}

export default obj